.itemcomponents{
    width: calc(100% / 6);;
    
    border-right: 2px solid black;
    border-bottom: 2px solid black;
}

.listul {
    list-style: disc outside none;
    display: list-item;
    margin-left: 2rem;
    }

.itbutton {
    appearance: none; background-color: #2ea44f;
    border: 1px solid rgba(27, 31, 35, .15); border-radius: 6px;
    box-shadow: rgba(27, 31, 35, .1) 0 1px 0;box-sizing: border-box;
    color: #fff; cursor: pointer; display: inline-block;
    font-family: -apple-system,system-ui,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji";
    font-size: 14px; font-weight: 600;
    line-height: 20px;padding: 1px 8px;
    position: relative;
    text-align: center;
    text-decoration: none;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    vertical-align: middle;
    white-space: nowrap;
    }

