.Combined {
    left:500px;
    float: right;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 80vh;
    width: 80%;
    background-color: #faf9f6;
  }
  
.Combined .btn {
    right: 0;
    margin-top: 1rem;
    border: none;
    background-color: #1d4ed8;
    color: #fff;
    padding: 0.5rem 1rem;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 0px 5px 5px #ccc;
  }